                  iconBg={textColor}
                  stat="Daily Spends"
                  statTitle="Log and analyze your daily spends."
                  options={quaterlySales}
                  series={quaterlySalesSeries}
                  type="area"
                  hideChart={true} //{window.screen.width < 500 ? true : false}
